}

/* If you also want to add a subtle border or shadow: */
.blog-post-content img {
  max-width: 100%;
  height: auto;
  display: block;
  margin: 2rem auto;
  border-radius: 8px;  /* Optional: rounds the corners */
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);  /* Optional: adds subtle shadow */
}

/* If you want to add a caption style: */
.blog-post-content img + em {
  display: block;
  text-align: center;
  color: #666;
  font-size: 0.9rem;
  margin-top: -1rem;
  margin-bottom: 2rem;
}

